* Timeline box
*/.timeline .date {	font-weight: 500;}.timeline .line {	position: relative;	height: 17px;	border-left: 1px solid #aeb1be;}.timeline .line:before {	content: '';	position: absolute;	top: 50%;	left: 0;	height: 2px;	transform: translateY(-50%);	border-top: 1px solid #aeb1be;	width: 100vw;}.timeline a.media-item {	position: relative;	overflow: hidden;	display: block;	border-radius: 6px;	max-width: 100px;}.timeline a.media-item:before {	content: "\f504";	position: absolute;	top: 50%;	left: 50%;	z-index: 2;	transform: translate(-50%, -50%);	font-family: "Material Design Icons";	font-size: 30px;	line-height: 1;	color: #ffffff;	transition: .3s;	will-change: transform;}.timeline a.media-item:after {	content: "";	position: absolute;	left: 0;	top: 0;	z-index: 1;	width: 100%;	height: 100%;	background: rgba(123, 89, 208, 0.7);	transition: .3s;}.timeline * + .line {	margin-top: 9px;}.timeline * + .media-item {	margin-top: 19px;}.timeline * + .title {	margin-top: 17px;}.timeline * + .exeption {	margin-top: 17px;}.desktop .timeline a.media-item:before {	color: transparent;	transform: translate(-50%, -50%) scale(0.5);}.desktop .timeline a.media-item:after {	background: rgba(123, 89, 208, 0);}.desktop .timeline a.media-item:hover:before {	color: #ffffff;	transform: translate(-50%, -50%) scale(1);}.desktop .timeline a.media-item:hover:after {	background: rgba(123, 89, 208, 0.7);}/*
